import { Component, OnInit, ViewChild, HostListener } from '@angular/core';
import { collapsible } from '@app/shared';
import { DashboardService } from '../../dashboard.service';
import { MatSidenav } from '@angular/material';
import { Page, Section } from './interfaces';
import { Router, NavigationEnd } from '@angular/router';
import { UtilService } from '@app/core';

@Component({
	selector: 'tr-sidenav',
	templateUrl: './sidenav.component.html',
	styleUrls: ['./sidenav.component.scss'],
	animations: [collapsible]
})
export class SidenavComponent implements OnInit {
	
	constructor(
		private dashboardService: DashboardService,
		private utilService: UtilService,
		private router: Router
		) { }
		
	lastUrl: string;

	user = this.utilService.getLS('user', true);

	sections: Section[] = [
		{
			title: 'inicio',
			children: [
				{ url: '/inicio', title: 'inicio', icon: 'home' },
				{ url: '/reactive', title: 'reactive', icon: 'text_fields' },
				{
					title: 'dropdown',
					icon: 'clear_all',
					pages: [
						{ url: '/pagina', title: 'pagina' }
					]
				}
			]
		}
	];

	@ViewChild(MatSidenav) snav: MatSidenav;

	ngOnInit() {
		if (document.documentElement.clientWidth <= 600) {
			setTimeout(() => {
				this.snav.close();
			});
		}
		this.dashboardService.onSidenavToggle.subscribe(() => {
			this.snav.toggle();
		});
		this.sections.forEach((section: Section) => {
			section.children.forEach((child: any) => {
				if (child.pages) {
					child.pages.forEach((page: Page) => {
						if (window.location.pathname.includes(page.url)) {
							child.expanded = true;
						}
					});
				}
			});
		});
		this.dashboardService.isSnavOpened = this.snav.opened;
		this.router.events.subscribe((event) => {
			if (event instanceof NavigationEnd) {
				if (window.innerWidth <= 600 && this.snav.opened) {
					this.snav.close();
				}
			}
		})
	}

	isActive(page: Page) {
		const url = this.router.url.indexOf('?') !== -1 ? this.router.url.substring(0, this.router.url.indexOf('?')) : this.router.url;
		if (url === page.url) {
			this.lastUrl = page.url;
		}
		return this.lastUrl === page.url;
	}

	@HostListener('window:resize', ['$event'])
	onResize(event) {
		if (event.target.innerWidth <= 600 && this.snav.opened) {
			this.snav.close();
		}
	}

}
